import React, { useEffect } from "react";
import Carousel from "react-elastic-carousel";
import Item from "./Item";
import Sdata from './Sdata'


const breakPoints = [
  { width: 1, itemsToShow: 1 },
  { width: 550, itemsToShow: 1 },
  { width: 900, itemsToShow: 2 },
  { width: 1200, itemsToShow: 2 },
];

const Card = () => {
useEffect(()=>{
let pricing_state='true'
  var pricing_more=document.getElementsByClassName('pricing-more')
  let lenth=pricing_more.length
  for(let i=0;i<pricing_more.length;i++){
    pricing_more[i].addEventListener('click',()=>{
      var card_discription=document.getElementsByClassName('card-discription')
      var order_btn=document.getElementsByClassName('order-btn')
      if(pricing_state==='true'){
      for(let j=0;j<card_discription.length;j++){
        card_discription[j].style.display='block'
        order_btn[j].style.display='block'
        for(let m=0;m<lenth;m++){
          pricing_more[m].innerHTML='Less'
        }
        pricing_state='false'
      }
     }else{
      for(let k=0;k<card_discription.length;k++){
        card_discription[k].style.display='none'
        order_btn[k].style.display='none'
        for(let m=0;m<lenth;m++){
          pricing_more[m].innerHTML='More'
        }
        pricing_state='true'
      }
     }
    },[])
  }


})
  return (
    <>
      <div>
      <div className="App ">
        <Carousel className='over1' breakPoints={breakPoints}>
       {
        Sdata.map((val)=>{
          return(
           <div >
           <Item className='carousel1-container' id='carousel1' >
              <div className='carousel1-container1' >
              <p className='card-head'>{val.name}</p>
               <img className='card-img' src={val.img} alt=''/>
               <span className='stars'>{val.stars}</span>
               <span className='last-updated'>updatetd {val.updated} </span>
               <h1 className='card-pricing'>{val.pricing}</h1> 
              </div>
               <div className='carousel1-container2'>
                <div>
                  <p className='card-discription'>{val.discription}</p>
                <a className='order-btn'>Order Now</a>
                </div>
               </div>
               <h1 className='pricing-more'>More</h1>
            </Item>
           </div>
          );
        })
      }
        </Carousel>
      </div>
   </div>
    </>
  );
};

export default Card;
