import React from "react";
import Pricingfrag from './Pricingfrag'
import iphone from './images/exclussivephone.png'
import surface_studio from './images/exclussive surfacestudio.png'
import surface_pro from './images/exclussive surfacepro front.png'
const Pricing = () => {
  return (
    <>
      <div className='main-pricing' >
      <div className='pricing-search-bar'>
        <form>
          <input className='pricing-text-input' type='text' placeholder='Seach for items' required/>
          <input className='pricing-btn-input' type='submit' value='search'></input>
        </form>
      </div>
      <div className="pricing-container">
        <div className="main-container-margin">
          <div className="Common-container">
            <div className='card-group'><Pricingfrag></Pricingfrag></div>
          </div>
        </div>
      </div>
      <div className='exclussive exlussive1'>
       <div>
       <h1>Iphone 13 pro MAX++</h1>
        <h2>From 1,00,000INR </h2>
        <p>Inc. offer on MASTER CARD.</p>
        <button>BUY NOW</button>
        <h3>Know More</h3>
       </div>
        <img src={iphone} alt='exclussive offer'/>
       
      </div>
      <div className='exclussive exlussive2'>
        <div>
        <h1>Suface studio MAX</h1>
        <h2>From 1,10,000INR </h2>
        <p>Inc. offer on MASTER CARD.</p>
        <button>BUY NOW</button>
        <h3>Know More</h3>
        </div>
         <img src={surface_studio} alt='exclussive offer'/>
      </div>
      <div className='exclussive exlussive3'>
        <div>
        <h1>Surface PRO</h1>
        <h2>From 80,000INR </h2>
        <p>Inc. offer on MASTER CARD.</p>
        <button>BUY NOW</button>
        <h3>Know More </h3>
        </div>
         <img src={surface_pro} alt='exclussive offer'/>
      </div>
      </div>
    </>
  );
};
export default Pricing;
