import React, { useEffect } from "react";
import Logo from "./images/logo.png";
import { NavLink } from "react-router-dom";

const Navfrag = () => {

  useEffect(()=>{
    var menu_state='true'
    document.getElementById('listenmenu').addEventListener('click',()=>{
     if(menu_state==='true'){
       document.getElementById('lineid').classList.add('open')
       document.getElementById('nav-list-id').style.transform='translate(15%)'
       menu_state='false'
     }else{
      document.getElementById('lineid').classList.remove('open')
      document.getElementById('nav-list-id').style.transform='translate(100%)'
      menu_state='true'
     }
    })
  })
  return (
    <>
      <div className="nav-main-continer">
        <div className="nav-container">
          <img className="nav-img" src={Logo} alt="logo" />
          <ul className="nav-list" id='nav-list-id'>
            <li className="list-items">
              <NavLink activeClassName='navactive' id='navlinkid' className='navlink' to="/">Home</NavLink>
            </li>
            <li className="list-items">
              <NavLink activeClassName='navactive' id='navlinkid' className='navlink' to="/pricing">Pricing</NavLink>
            </li>
            <li className="list-items">
              <NavLink activeClassName='navactive' id='navlinkid' className='navlink' to="/about">AboutUs</NavLink>
            </li>
            <li className="list-items">
              <NavLink activeClassName='navactive' id='navlinkid' className='navlink' to="/signin">SignIn</NavLink>
            </li>
          </ul>
          <div className='handburger' id='listenmenu'>
               <div className='line ' id='lineid'></div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Navfrag;
